.hero{position:relative}.hero__wrapper:before{content:"";display:block;padding-top:101.33333%}.hero__wrapper{position:relative;display:flex}.hero__block-image-wrapper,.hero__block-video-wrapper{left:0;right:0;position:absolute;top:0;bottom:0;z-index:1}.hero__block-image--desktop{display:none}.hero__block-content{z-index:2;margin:auto;padding:12px;width:100%}.hero__inner-content{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-wrap:wrap;flex:1 0 auto;width:100%;height:100%}.hero__image{left:0;right:0;position:absolute;top:0;bottom:0}.hero__description,.hero__title{color:var(--c-white);text-align:center}.hero__description{width:100%;margin-top:10px}.hero__cta{margin-top:23px}.hero__button{display:inline-block}.hero__video{left:0;right:0;position:absolute;top:0;bottom:0}.hero--container .hero__block-content{padding-left:var(--container-edge);padding-right:var(--container-edge);margin-left:auto;margin-right:auto;width:100%;max-width:var(--container-max-width)}.hero--two-up .hero__block-image-wrapper:before{content:"";display:block;padding-top:100%}.hero--two-up .hero__block-image-wrapper{position:relative;width:100%}.hero--two-up .hero__wrapper{display:flex;flex-direction:column;align-items:center;justify-content:center;flex-wrap:wrap}.hero--two-up .hero__wrapper:before{content:none}.hero--two-up .hero__block-content{width:100%;flex:1 1 auto}.hero--two-up .hero__description,.hero--two-up .hero__title{color:var(--c-black)}